.slide {
    transition: opacity 0.5s;
    opacity: 1;
}

.slide.hide {
    opacity: 0;
    pointer-events: none;
}

.someslide {
    transition: opacity 0.5s;
    opacity: 1;
}

.but1 {
    width: 168px;
    height: 730px;
    z-index: 0;
    position: absolute;
    top: 0px;
    opacity: 0;
}
.but2 {
    width: 168px;
    height: 730px;
    z-index: 0;
    position: absolute;
    top: 0px;
    left: 168px;
    opacity: 0;
}

.outer {
    position: relative;
}

.nullpos1 {
    z-index: 2;
    position: absolute;
    top: 0px;
    /* left: 0px; */
}

.nullpos2 {
    position: relative;
    /* left: 0px; */
}